.Navbar {
  position: relative;
  display: none;
}

.Navbar:target {
  display: block;
}

.Navbar ul {
  padding: 0;
}

.Navbar ul > li {
  list-style: none;
  padding: 0;
}
.Header {
  background-color: #282c34;
  color: white;
}

.Header > .Title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  height: 46px;
  border-bottom: 1px solid #eee;
}

.Header > button,
.Header > a {
  width: 46px;
  height: 46px;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
}

.Header > button {
  background: none;
  border: none;
}
.Layout {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background:  #ddd;
}

.Container {
  margin: 0 auto;
  width: 100%;
  max-width: 1024px;
  flex-grow: 1;
  height: auto;
  background: #eee;
}
.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  max-width: 450px;
  margin: 0 auto;
}

.Home > button, 
.Home > div {
  margin-bottom: 20px;
  width: 100%;
}

.Separator {
  width: 100%;
  position: relative;
  height: 20px;
}

.Separator:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  top: 50%;
  left: 0;
  background: #aaa;
}

.Separator > span {
  padding: 0 5px;
  background: #eee;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Item {
  width: 100%;
  max-width: 300px;
  padding: 20px;
  background: pink;
}
.Calendar {
  max-width: 450px;
  margin: 5px auto;
  display: flex;
  box-shadow: 0px 5px 15px 0px rgba(204,204,204,1);
}

.Calendar-container {
  flex-grow: 1;
}

.Calendar-current-date {
  padding: 10px;
  border: 1px solid #aaa;
}

.Calendar-weeks {
  display: flex;
  border: 1px solid #aaa;
  border-top: 0px;
  height: 35px;
}

.Calendar-weeks > div {
  display: flex;
  flex-grow: 1;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.Calendar-days {
  display: grid;
  grid-template-columns: repeat(7, calc(calc(100%/7) - 1px));
  grid-auto-rows: 40px;
  grid-gap: 1px;
}

.Calendar-days > div {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.Calendar-days > div.is-disabled {
  opacity: 0.6;
}

.DriversList {
  display: flex;
  justify-content: center;
}

.Driver-photo {
  width: 56px;
  height: 56px;
  border: 1.5px solid transparent;
  border-radius: 50%;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2em;
  color: #111;
  box-shadow: 0px 5px 15px 0px rgba(204,204,204,0.75);
}
.BalanceGraph {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 25px;
}

.BalanceGraph > div {
  box-shadow: 0px 5px 15px 0px rgba(204,204,204,0.7);
}
.CreateGroupForm {
  display: flex;
  flex-direction: column;
  padding: 20px;
  max-width: 450px;
  margin: 0 auto;
}

.CreateGroupForm > div {
  margin-bottom: 20px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

a:-webkit-any-link {
  color: inherit;
  text-decoration: none;
}

